<template>
	<div style="margin-top: 30px; ">
		<div id="container"></div>
	</div>
</template>

<script>
	import { Bar } from '@antv/g2plot';
	export default {
		data() {
			return {
				dataSource: [],
			};
		},
		methods: {
			async getHotMovieData() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + '/admin/hot_movie/chart');
				this.dataSource = data.data;

				console.log(this.dataSource)
			},
		},
		async created() {
			this.getHotMovieData();
		},
		watch: {
			dataSource: async function(data) {
				const bar = new Bar('container', {
				  data,
				  xField: 'ecount',
				  yField: 'name',
				  seriesField: 'name',
				  legend: {
				    position: 'top-left',
				  },
				});
				
				bar.render();
			}
		},

	}
</script>

